                <span class="lmd"> {{model.lmd}}  </span>
                <span class="jq">{{model.jq}}</span>
            </diV>
            <div>
                <span class="ly">{{model.ly}}月</span>
                <span class="ld">{{model.ld}}日</span>
            </div>
        </div>
        <div class="ss">
            <span class="tt">值神</span><span>{{model.zs}}</span>
            <span :style="{marginLeft: '20px'}" class="tt">建除</span><span>{{model.jcses}}</span>
            <!--<div>
                <span class="tt">诸神方位</span>
            </div>-->
            <div :style="{fontSize:'16px', textAlign:'center'}">
                {{model.zsfw}}
            </div>
        </div>
        <!--<div class="rq">
            <div class="item1">
                {{model.d}}
            </div>
            <div class="item2">
                <div class="nl">
                    <span class="lmd">{{model.lmd}}</span>
