        <hr/>
        <div class="yj">
            <div>【通书六十事】</div>
            <div><span class="yjy">宜</span>{{model.yjy1}}</div>
            <div><span class="yjj">忌</span>{{model.yjj1}}</div>
        </div>
        <div class="yj">
            <div>【民用三十七事】</div>
            <div><span class="yjy">宜</span>{{model.yjy3}}</div>
            <div><span class="yjj">忌</span>{{model.yjj3}}</div>
        </div>
        <div class="yj">
            <div>【御用六十七事】</div>
            <div><span class="yjy">宜</span>{{model.yjy2}}</div>
            <div><span class="yjj">忌</span>{{model.yjj2}}</div>
        </div>
        <!--<div class="yj">
          <div>【所有词条】</div>
          <div><span class="yjy">宜</span>{{model.yjy}}</div>
          <div><span class="yjj">忌</span>{{model.yjj}}</div>
        </div>-->
    </div>
</template>

<script>


    export default {
        props: {
            data: Object
        },
        watch: {
            data: function (v) {
                this.item = v;
            }
        },
        data() {
            return {
                item: this.data,

            }
        },
        computed: {
            model() {
                let model = {};
                if (!this.item) {
                    return {};
                }
                const weeks = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
                let ob = this.item.lun;
                console.log(ob)
                model.ymd = ob.format('YYYY年MM月DD日')
                model.xiz = weeks[ob.dayOfWeek]
                let lm = ob.lunar.getMonthName();
                if (ob.lunar.month === 11) {
                    lm = '冬月'
                    if (ob.lunar.isLeapMonth) {
                        lm = '闰冬月'
                    }
                } else if (ob.lunar.month === 12) {
                    lm = '腊月'
                    if (ob.lunar.isLeapMonth) {
                        lm = '闰腊月'
                    }
                }
                model.lmd = lm + ob.lunar.getDayName();
                model.jq = ob.solarTerm?.toString()
                model.d = ob.day < 10 ? "0" + ob.day : ob.day
                model.ly = ob.char8.month
                model.ld = ob.char8.day
                // 通书六十事
                model.yjy1 = ob.theGods.query('good act 1').toString()
                model.yjj1 = ob.theGods.query('bad act 1').toString()
                // 御用六十七事
                model.yjy2 = ob.theGods.query('good act 2').toString()
                model.yjj2 = ob.theGods.query('bad act 2').toString()
                // 民用三十七事
                model.yjy3 = ob.theGods.query('good act 3').toString()
                model.yjj3 = ob.theGods.query('bad act 3').toString()
                model.yjy = ob.theGods.query('good act').toString()
                model.yjj = ob.theGods.query('bad act').toString()
                // 值神
                model.zs = ob.theGods.getBy12God('day');
                // 建除十二神
                model.jcses = ob.theGods.getDuty12God() + '日';
                model.zsfw = "";
                const allDirections = ob.theGods.getAllLuckDirection()
                for (let i = 0; i < allDirections.length; i++) {
                    const [d24, god] = allDirections[i]
                    let dd = d24.direction;
                    if (dd.length === 1) {
                        dd = "正" + dd;
                    }
                    model.zsfw += god.name + "-" + dd + " "
                }
                return model;
            },
        },
        created() {

        },
        methods: {}
    }
</script>

